import { CrosswordCell } from "./CrosswordCell";

export class CrosswordWord {

    public word: string;
    public vertical: boolean;
    public letterCells: CrosswordCell[];

    constructor(word: string, vertical: boolean, letterCells: CrosswordCell[]) {
        this.word = word;
        this.vertical = vertical;
        this.letterCells = letterCells;
    }
}

